<template>
  <a-row style="margin: 0 -12px">
    <a-col style="padding: 0 12px" :xl="10" :lg="24" :md="24" :sm="24" :xs="24">
      <a-card
        :bodyStyle="{
          height: '800px',
          'padding-right': '1px',
          'padding-left': '1px',
          'padding-top': '1px',
          'padding-bottom': '0px',
        }"
        title=""
      >
        <template #extra>
          <a-button @click="compile" type="primary"
            ></a-button
          ></template
        >
        <a-alert
          :message="successMsg"
          type="success"
          show-icon
          v-show="isSuccess"
        />
        <a-alert
          :message="errorMsg"
          type="warning"
          show-icon
          v-show="isError"
        />
        <codemirror v-model="code" :options="cmOptions" />
      </a-card>
    </a-col>
    <a-col style="padding: 0 12px" :xl="14" :lg="24" :md="24" :sm="24" :xs="24">
      <a-card title="">
        <v-chart
          class="chart"
          :option="option"
          autoresize
          :update-options="{
            notMerge: true,
            lazyUpdate: false,
            replaceMerge: ['xAxis', 'yAxis', 'series'],
          }"
          ref="chart"
        />
      </a-card>
    </a-col>
  </a-row>
</template>
<script>
const isGridOn = (grid) => {
  return grid === 1 ? true : false;
};

import VChart from "vue-echarts";
import "codemirror/mode/javascript/javascript.js";
import "codemirror/theme/idea.css";
// require active-line.js
import "codemirror/addon/selection/active-line.js";

// styleSelectedText
import "codemirror/addon/selection/mark-selection.js";
import "codemirror/addon/search/searchcursor.js";

// hint
import "codemirror/addon/hint/show-hint.js";
import "codemirror/addon/hint/show-hint.css";
import "codemirror/addon/hint/javascript-hint.js";
import "codemirror/addon/selection/active-line.js";

// highlightSelectionMatches
import "codemirror/addon/scroll/annotatescrollbar.js";
import "codemirror/addon/search/matchesonscrollbar.js";
import "codemirror/addon/search/searchcursor.js";
import "codemirror/addon/search/match-highlighter.js";

// keyMap
import "codemirror/mode/clike/clike.js";
import "codemirror/addon/edit/matchbrackets.js";
import "codemirror/addon/comment/comment.js";
import "codemirror/addon/dialog/dialog.js";
import "codemirror/addon/dialog/dialog.css";
import "codemirror/addon/search/searchcursor.js";
import "codemirror/addon/search/search.js";
import "codemirror/keymap/sublime.js";

// foldGutter
import "codemirror/addon/fold/foldgutter.css";
import "codemirror/addon/fold/brace-fold.js";
import "codemirror/addon/fold/comment-fold.js";
import "codemirror/addon/fold/foldcode.js";
import "codemirror/addon/fold/foldgutter.js";
import "codemirror/addon/fold/indent-fold.js";
import "codemirror/addon/fold/markdown-fold.js";
import "codemirror/addon/fold/xml-fold.js";
export default {
  name: "Compiler",
  data() {
    return {
      code: `// 
ORIGIN IS (80,70);  -- Tab
//// 
SCALE is (1,1);
-- 
ROT IS 3/(2*PI);
--------- 
FOR T FROM 1 TO 1000 STEP 1 DRAW(T+5**(2-3)/4,sin(T/30)) IN DIMGRAY LINE 3.5 DOTS;
XLABEL '';
YLABEL 'GDP';   -- 
TITLE 'GDP';
GRID ON;
DYNAMIC ON;
// 
// `,
      cmOptions: {
        tabSize: 4,
        styleActiveLine: false,
        lineNumbers: true,
        styleSelectedText: false,
        line: true,
        foldGutter: true,
        gutters: ["CodeMirror-linenumbers", "CodeMirror-foldgutter"],
        highlightSelectionMatches: { showToken: /\w/, annotateScrollbar: true },
        mode: "text/javascript",
        matchBrackets: true,
        showCursorWhenSelecting: true,
        theme: "idea",
      },
      option: {},
      updateOption: {},
      isImage: false,
      isError: false,
      isSuccess: false,
      errorMsg: "",
      successMsg: "",
      collapsed: false,
    };
  },
  methods: {
    onCmReady(cm) {
      console.log("the editor is readied!", cm);
    },
    onCmFocus(cm) {
      console.log("the editor is focused!", cm);
    },
    onCmCodeChange(newCode) {
      console.log("this is new code", newCode);
      this.code = newCode;
    },
    getLineType(lineType) {
      if (lineType == "SOLID") {
        return "solid";
      } else if (lineType == "DOTS") {
        return "dotted";
      } else if (lineType == "DASHED") {
        return "dashed";
      }else if (lineType === "scatter"){
          return "scatter"
      }
    },
    compile() {
      const successMsg = "";

      var myHeaders = new Headers();
      myHeaders.append("Content-Type", "text/plain");

      var raw = this.code;

      var requestOptions = {
        method: "POST",
        headers: myHeaders,
        body: raw,
      };
      console.log("test");
      fetch(
        "https://service-dy26wz1i-1301774019.gz.apigw.tencentcs.com/release/APIService-1639560055",
        requestOptions
      )
        .then((response) => response.json())
        .then((result) => {
          if (result.error_code === 0) {
            this.successMsg = successMsg;
            this.isSuccess = true;
            this.isError = false;
            this.draw(result.result);
          } else {
            this.isSuccess = false;
            this.isError = true;
            this.errorMsg = "" + result.msg;
          }
        })
        .catch((error) => {
          this.errorMsg = "";
          this.isSuccess = false;
          this.isError = true;
        });
    },
    draw(data) {
      this.$refs.chart.clear();
      const lines = data.lines;
      let series = [];
      for (let line of lines) {
        console.log(line);
        const color = line.color;
        const width = line.width || 2;
        const lineType = this.getLineType(line.lineType);
        const dots = line.dots;
        const xys = [];
        if (lineType === "scatter") {
          for (let dot of dots) {
            xys.push([dot.x, dot.y, dot.r]);
          }
          series.push({
            data: xys,
            type: "scatter",
            symbolSize: function (data) {
              return data[2]
            },
            itemStyle: {
              color,
            },
            lineStyle: {
              color,
            },
          });
        } else {
          for (let dot of dots) {
            xys.push([dot.x, dot.y]);
          }
          series.push({
            data: xys,
            type: "line",
            // symbol: 'circle',
            itemStyle: {
              color,
            },
            lineStyle: {
              width,
              color,
              type: lineType,
            },
          });
        }
      }

      const option = {
        animationDuration: data.dynamic === 0 ? 0 : 3000,
        toolbox: {
          right: 100,
          feature: {
            saveAsImage: {
              name: data.title,
              pixelRatio: 2,
            },
          },
        },
        title: {
          text: data.title,
          left: "center",
        },
        xAxis: {
          name: data.xLabel,
          type: "value",
          axisPointer: {
            snap: true,
          },
          splitLine: {
            show: isGridOn(data.grid),
          },
        },
        yAxis: {
          name: data.yLabel,
          type: "value",
          splitLine: {
            show: isGridOn(data.grid),
          },
        },
        tooltip: {
          trigger: "item",
          formatter: "{c0}",
        },
        series,
      };

      this.option = option;
      this.isImage = true;
      console.log(this.option);
    },
  },
  computed: {},
  mounted() {
    console.log("the current CodeMirror instance object:", this.codemirror);
    // you can use this.codemirror to do something...
  },
  components: {
    VChart,
  },
};
</script>

